.feedback {

	// position: relative;
	&__br2 {
		display: none;

		@media(max-width:768px) {
			display: block;
		}
	}

	&__br1 {
		@media(max-width:768px) {
			display: none;
		}
	}

	&__content {
		z-index: 5;

		.svg-1 {
			z-index: 5;
			position: relative;
			position: absolute;
			left: 0;
			top: 50%;
			transform: translateY(-50%);

			@media(max-width:1480px) {
				width: 600px;
			}

			@media(max-width:1280px) {
				width: 400px;
			}

			@media(max-width:992px) {
				width: 300px;
			}

			@media(max-width:768px) {
				width: 200px;
			}

			path {
				opacity: 0;
			}
		}

		&--subtitle {
			font-size: 24px;
			font-weight: 300;
			margin-bottom: 30px;

			@media(max-width:686px) {
				font-size: 20px;
			}
		}

		&--wrapper {
			padding: 50px 100px 50px 50px;

			border-bottom: 1px solid #272C38;
			position: relative;

			@media(max-width:1600px) {
				padding: 50px 100px 50px 50px;
			}

			@media(max-width:768px) {
				padding: 50px 50px 50px 50px;
				display: flex;
				flex-direction: column;
				align-items: center;
			}

			&:before {
				position: absolute;
				content: "";
				width: 2px;
				left: 0;
				bottom: 0;
				height: 100%;
				background: linear-gradient(to bottom, #171C29 0%, #CBC9CA 0%, #272C38 100%);
			}

			&:after {
				position: absolute;
				content: "";
				width: 1px;
				right: 0;
				bottom: 0;
				height: 100%;
				background: linear-gradient(to bottom, #171C29 0%, #CBC9CA 0%, #272C38 100%);
			}

			h4 {
				font-size: 64px;
				margin-bottom: 7px;

				@media(max-width:1600px) {
					font-size: 55px;
				}

				@media(max-width:992px) {
					font-size: 45px;
				}

				@media(max-width:768px) {
					font-size: 35px;
				}

				@media(max-width:686px) {
					font-size: 35px;
				}
			}

			.btn {
				font-size: 16px;
			}
		}
	}

	&__section {
		position: relative;
		display: flex;
		min-height: 280px;
		align-items: center;

		@media(max-width:1024px) {
			min-height: 200px;
		}

		@media(max-width:880px) {
			min-height: 150px;
		}

		@media(max-width:768px) {
			min-height: 100px;
			flex-wrap: wrap;
		}

		&:first-child {

			.feedback__img {
				position: absolute;
				right: -200px;
			}
		}

		&:not(:last-child) {
			margin-bottom: 40px;
		}

		&:nth-child(2) {
			@media(max-width:1280px) {
				flex-direction: row-reverse;
			}

			.feedback__content {
				width: 100%;
			}

			.feedback__img {
				left: -200px;

				@media(max-width:1280px) {
					position: absolute;
					left: 0;
				}
			}

			.feedback__content--text {
				z-index: 3;
				position: relative;
				display: flex;
				justify-content: flex-end;
				align-items: center;
				width: 100%;

				.anim-text {
					&>div {
						width: 100%;
					}
				}

				span {
					-webkit-text-fill-color: transparent;
					-webkit-text-stroke: 1px #272C38;
					font-size: 126px;
					text-transform: uppercase;
					font-weight: 700;
					opacity: 0;

					@media(max-width:1650px) {
						font-size: 100px;
					}

					@media(max-width:1480px) {
						font-size: 80px;
					}

					@media(max-width:1280px) {
						font-size: 60px;
					}

					@media(max-width:992px) {
						font-size: 55px;
					}

					@media(max-width:768px) {
						font-size: 45px;
						text-align: center;
					}

					@media(max-width:686px) {
						font-size: 35px;
					}
				}

				.space {
					display: inline-block;
					min-width: 35px;

					@media(max-width:1650px) {
						min-width: 25px;
					}

					@media(max-width:1480px) {
						min-width: 15px;
					}
				}
			}
		}

		&:nth-child(3) {
			.feedback__img {
				position: relative;
			}

			@media(max-width:1480px) {
				flex-direction: column;
				gap: 30px;

				.feedback__img {
					width: 100%;
					left: 0;

					img {
						width: 100%;
						object-fit: cover;
					}
				}

				.feedback__content {
					width: 100%;

					&--wrapper {
						padding: 0 50px 50px 50px;
						text-align: center;
					}
				}
			}
		}

		.feedback__img {
			position: absolute;
			top: 0;
			right: -200px;
			min-height: 100px;

			@media(max-width:1280px) {
				img {
					width: 100%;
					min-height: 100px;
					height: 100%;
					object-fit: cover;
					height: 100%;
				}

				position: relative;
				right:0 !important;
				width:850px;
			}

			@media(max-width:1024px) {
				width: 600px;
			}

			@media(max-width:880px) {
				width: 450px;
			}

			@media(max-width:768px) {
				width: 350px;
			}

			@media(max-width:576px) {
				width: 250px;
			}

			@media(max-width:480px) {
				width: 200px;
			}

			@media(max-width:430px) {
				width: 150px;
			}
		}
	}

	&__img {
		display: inline-block;
		width: fit-content;

		img {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			opacity: 0;
		}

		.hidden {
			position: static;
			transform: unset;
		}

		&--1 {
			img {
				&:nth-child(2) {
					animation: slideAnim1 6s linear infinite;
				}

				&:nth-child(3) {
					animation: slideAnim2 6s linear infinite;
				}

				&:nth-child(4) {
					animation: slideAnim3 6s linear infinite;
				}
			}
		}

		&--2 {
			img {
				&:nth-child(2) {
					animation: slideAnim1 6s linear 0.5s infinite;
				}

				&:nth-child(3) {
					animation: slideAnim2 6s linear 0.5s infinite;
				}

				&:nth-child(4) {
					animation: slideAnim3 6s linear 0.5s infinite;
				}
			}
		}

		&--3 {
			img {
				&:nth-child(2) {
					animation: slideAnim1 6s linear 1s infinite;
				}

				&:nth-child(3) {
					animation: slideAnim2 6s linear 1s infinite;
				}

				&:nth-child(4) {
					animation: slideAnim3 6s linear 1s infinite;
				}
			}
		}
	}

	@keyframes slideAnim1 {
		0% {
			opacity: 1;
		}

		16.6% {
			opacity: 1;
		}

		33.2% {
			opacity: 0;
		}

		49.8% {
			opacity: 0;
		}

		66.4% {
			opacity: 0;
		}

		83% {
			opacity: 0;
		}

		100% {
			opacity: 1;
		}
	}

	@keyframes slideAnim2 {
		0% {
			opacity: 0;
		}

		16.6% {
			opacity: 0;
		}

		33.2% {
			opacity: 1;
		}

		49.8% {
			opacity: 1;
		}

		66.4% {
			opacity: 0;
		}

		83% {
			opacity: 0;
		}

		100% {
			opacity: 0;
		}
	}

	@keyframes slideAnim3 {
		0% {
			opacity: 0;
		}

		16.6% {
			opacity: 0;
		}

		33.2% {
			opacity: 0;
		}

		49.8% {
			opacity: 0;
		}

		66.4% {
			opacity: 1;
		}

		83% {
			opacity: 1;
		}

		100% {
			opacity: 0;
		}
	}
}