@import './_banner';
@import './_feedback';

.banner {
  background: url('@images/about.png') center no-repeat;
  min-height: 807px;

  @media(max-width: 1280px) {
    min-height: 760px;
  }

  @media(max-width: 992px) {
    min-height: 716px;
  }

  &__content {
    text-align: left;
  }
}

.about {
  &__title {
    font-size: 40px;
    font-weight: 700;
    color: #fff;
    margin-bottom: 70px;

    @media(max-width: 992px) {
      font-size: 36px;
      margin-bottom: 60px;
    }

    @media(max-width: 578px) {
      font-size: 32px;
      margin-bottom: 50px;
    }
  }

  &__desc {
    font-size: 32px;
    font-weight: 300;
    color: #fff;
    margin-bottom: 113px;
    line-height: 1.4;

    @media(max-width: 992px) {
      font-size: 26px;
      margin-bottom: 90px;
    }

    @media(max-width: 578px) {
      font-size: 20px;
      margin-bottom: 70px;
    }

    span {
      font-size: 32px;
      font-weight: 700;
      text-decoration: underline;

      @media(max-width: 992px) {
        font-size: 26px;
      }

      @media(max-width: 578px) {
        font-size: 20px;
        text-decoration: none;
      }
    }
  }
}

.info {
  padding-top: 140px;

  @media(max-width: 992px) {
    padding-top: 95px;
  }

  @media(max-width: 578px) {
    padding-top: 50px;
  }

  &__top {
    display: flex;
    justify-content: space-between;
    gap: 45px;
    flex-wrap: wrap;
    margin-bottom: 60px;

    @media(max-width: 992px) {
      gap: 40px;
      margin-bottom: 50px;
    }

    @media(max-width: 578px) {
      margin-bottom: 40px;
    }
  }

  &__main {
    font-size: 32px;
    font-weight: 400;
    color: #272C38;
    max-width: 780px;

    @media(max-width: 992px) {
      font-size: 28px;
    }

    @media(max-width: 578px) {
      font-size: 24px;
    }
  }

  &__add {
    font-size: 20px;
    font-weight: 300;
    color: #272C38;
    max-width: 696px;
  }

  &__bottom {
    overflow: hidden;

    img {
      width: 100%;

      @media(max-width: 578px) {
        width: 120%;
        min-height: 244px;
        object-fit: cover;
        object-position: -90px 0%;
      }
    }
  }
}

.history-section {
  margin-top: 140px;
  margin-bottom: 170px;

  @media(max-width: 992px) {
    margin-top: 95px;
    margin-bottom: 250px;
  }

  @media(max-width: 578px) {
    margin-top: 50px;
  }

  &__title {
    position: absolute;
    z-index: 15;
    font-size: 64px;
    font-weight: 300;
    color: #272C38;
    // background: radial-gradient(farthest-corner at 0 0, #D4D4D4, #D4D4D4 30%, rgba(212, 212, 212, 0));
    padding: 0 250px 250px 0;

    @media(max-width: 992px) {
      font-size: 52px;
      margin-bottom: 20px;
      position: static;
      padding: 0 0 0 0;
    }

    @media(max-width: 578px) {
      font-size: 40px;
      margin-bottom: 40px;
    }
  }

  .histories {
    position: relative;
    margin-bottom: 182px;
    padding-left: 10%;
    padding-top: 9%;
    overflow: hidden;
    padding-bottom: 55px;

    &::-webkit-scrollbar {
      width: 0;
    }

    @media(max-width: 1224px) {
      padding-top: 15%;
    }

    @media(max-width: 992px) {
      padding-left: 0%;
      padding-top: 0%;
    }

    @media(max-width: 578px) {
      margin-bottom: 50px;
      padding-left: 0%;
    }

    &__gradient {
      content: '';
      position: absolute;
      left: -50%;
      bottom: 0%;
      height: 80%;
      width: 200%;
      background: linear-gradient(to bottom, rgba(203, 201, 202, 0), #D4D4D4);
      z-index: 5;
      pointer-events: none;

      @media(max-width: 992px) {
        opacity: 0.5;
      }

      @media(max-width: 578px) {
        opacity: 0;
      }
    }

    &__in {
      display: flex;
      flex-direction: column;
      margin-top: -9%;

      @media(max-width: 992px) {
        margin-top: 0%;
        align-items: stretch;
      }
    }
  }

  .history {
    position: relative;
    z-index: 1;
    box-sizing: border-box;

    &:hover {
      z-index: 5;
    }

    &__bg {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      border-bottom: 1px solid rgba(39, 44, 56, 1);
      border-top: 1px solid transparent;
      animation: dribbleRef 0.6s ease-in 0.1s forwards;

      @media(max-width: 578px) {
        background: linear-gradient(to bottom, rgba(201, 200, 200, 0), #D4D4D4) !important;
      }

      &::after,
      &::before {
        content: '';
        position: absolute;
        bottom: 0;
        width: 1px;
        background: linear-gradient(to bottom, rgba(203, 201, 202, 0), rgba(39, 44, 56, 1));
        height: 50%;
      }

      &::before {
        left: 0;
      }

      &::after {
        right: 0;
      }
    }

    &:hover .history__bg {
      border-top: 1px solid #C9C7C8;
      background: linear-gradient(to bottom, #E3E3E3, #D4D4D4) !important;
      // box-shadow: 40px 50px 120px 0 rgba(51, 51, 51, 0.2);
      animation: dribble 0.6s ease-out forwards;

      @media(max-width: 992px) {
        border-top: unset;
      }

      @media(max-width: 578px) {
        background: linear-gradient(to bottom, rgba(201, 200, 200, 0), #D4D4D4) !important;
        box-shadow: unset;
      }

      &::after,
      &::before {
        height: 100%;
      }
    }

    &--right {
      align-self: flex-end;
      padding: 3% 18% 9% 5%;

      @media(max-width: 992px) {
        align-self: stretch;
        padding: 3% 6% 15% 6%;
        margin-bottom: 25%;
      }
    }

    &--left {
      align-self: flex-start;
      padding: 3% 5% 12%;

      @media(max-width: 992px) {
        align-self: stretch;
        padding: 3% 6% 15% 6%;
        margin-bottom: 25%;
      }
    }

    &__year {
      position: relative;
      z-index: 2;
      font-size: 64px;
      font-weight: 700;
      color: #272C38;
      margin-bottom: 20px;

      @media(max-width: 992px) {
        font-size: 56px;
      }

      @media(max-width: 578px) {
        font-size: 48px;
      }
    }

    &__title {
      position: relative;
      z-index: 2;
      font-size: 36px;
      font-weight: 300;
      color: #272C38;
      margin-bottom: 20px;
      text-transform: uppercase;

      @media(max-width: 992px) {
        font-size: 28px;
      }

      @media(max-width: 578px) {
        font-size: 20px;
      }
    }

    &__text {
      position: relative;
      z-index: 2;
      font-size: 20px;
      font-weight: 300;
      color: #272C38;
      max-width: 70%;
      min-width: 550px;

      @media(max-width: 992px) {
        min-width: unset;
      }

      @media(max-width: 578px) {
        max-width: 100%;
      }
    }

    &__img {
      position: relative;

      img {
        max-width: 100%;

        @media(max-width: 992px) {
          max-width: 80vw;
        }
      }

      @media(max-width: 992px) {
        display: block;
        margin: 0 auto;
        margin-top: 15px;
      }
    }

    &__img-in {
      position: absolute;
      overflow: hidden;
    }

    &--right .history__img {
      position: relative;
      margin-top: -35px;

      @media(max-width: 992px) {
        margin-top: 15px;
      }
    }

    &--right .history__img-in {
      right: -50%;

      @media(max-width: 992px) {
        right: unset;
        left: 50%;
        transform: translateX(-50%);
      }
    }

    &--left .history__img {
      margin-top: 25px;
    }

    &--left .history__img-in {
      @media(max-width: 992px) {
        left: 50%;
        transform: translateX(-50%);
      }
    }

    &--1 {
      .history__img-in {
        right: -80%;
      }
    }

    &--3 {
      @media(max-width: 992px) {
        padding-bottom: 180px;
        margin-bottom: 15%;
      }

      .history__img-in {
        max-width: 400px;
      }
    }

    &--5 {
      @media(max-width: 992px) {
        padding-bottom: 180px;
        margin-bottom: 15%;
      }

      .history__img-in {
        max-width: 400px;
        right: -30%;
      }
    }

    &--6 {
      margin-bottom: 5%;

      @media(max-width: 992px) {
        padding-bottom: 200px;
        margin-bottom: 15%;
      }
    }

    &--7 {
      @media(max-width: 992px) {
        padding-bottom: 200px;
        margin-bottom: 15%;
      }
      .history__img-in {
        max-width: 400px;
        right: -30%;
      }
    }

    &--8 {
      margin-bottom: 5%;
      @media(max-width: 992px) {
        padding-bottom: 220px;
        margin-bottom: 19%;
      }

      .history__img-in {
        max-width: 400px;
      }
    }

    &--9 {
      @media(max-width: 560px) {
        margin-bottom: 3%;
      }
      .history__img-in {
        top: -25%;
        right: -30%;

        @media(max-width: 560px) {
          display: none;
        }

        .history__img-in2 {
          height: 100%;
          width: auto;
        }

        img {
          max-height: 320px;
        }
      }
    }

    &--10 {
      margin-bottom: 5%;

      .history__img-in {
        @media(max-width: 560px) {
          display: none;
        }
        img {
          max-height: 320px;
        }
      }
    }

    &--11 {
      @media(max-width: 992px) {
        padding-bottom: 120px;
      }
      .history__img-in {
        right: -35%;

        img {
          max-width: 320px;
        }
      }
    }

    &--12 {
      margin-bottom: 5%;
      @media(max-width: 992px) {
        padding-bottom: 200px;
        margin-bottom: 18%;
      }

      .history__img-in {
        img {
          max-width: 420px;
          @media(max-width: 992px) {
            max-width: 280px;
          }
        }
      }
    }

    &--13 {
      @media(max-width: 992px) {
        padding-bottom: 150px;
      }
      .history__img-in {
        right: -30%;

        img {
          max-width: 350px;
        }
      }
    }

    &--14 {
      @media(max-width: 992px) {
        padding-bottom: 150px;
      }
      .history__img-in {
        right: -10%;

        img {
          max-width: 350px;
        }
      }
    }

    &--15 {
      @media(max-width: 992px) {
        padding-bottom: 120px;
      }
      .history__img-in {
        right: -25%;

        img {
          max-width: 350px;
        }
      }
    }

    &--16 {
      margin-bottom: 12%;
      @media(max-width: 992px) {
        padding-bottom: 200px;
      }
    }

    &--17 {
      .history__img-in {
        right: -25%;

