        }
      }
    }

    &--18 {
      .history__img-in {
        img {
          max-width: 350px;
        }
      }
    }

    .history__img-in {
      @media(max-width: 992px) {
        right: unset;
        left: 50%;
        transform: translateX(-50%);

        img.wider {
          // max-width: 290px;
        }
      }
    }

