.sponsors-section {
  margin-top: 35px;
  margin-bottom: 140px;

  @media(max-width: 992px) {
    margin-bottom: 95px;
  }

  @media(max-width: 578px) {
    margin-bottom: 50px;
  }
}

.sponsors {
  &__items {
    display: flex;
    align-items: center;
    gap: 30px;
    justify-content: space-between;
    flex-wrap: wrap;
  }

  .sponsor {
    @media(max-width: 578px) {
      max-width: 124px;
    }

    img {
      width: 100%;
      display: block;
    }
  }
}