@use '../abstracts/variables' as var;

.banner {
	position: relative;
	z-index: 1;
	width: 100%;
	min-height: 995px;
	background-size: cover;
	display: flex;
	justify-content: center;
	align-items: flex-end;

	@media(max-width:1280px) {
		min-height: 850px;
	}

	@media(max-width:992px) {
		min-height: 717px;
	}

	&--col {
		justify-content: flex-start;
	}

	&__content {
		text-align: center;
		width: 100%;
		color: white;
		padding-bottom: 65px;

		@media(max-width:992px) {
			padding-bottom: 30px;
		}

		&--textWrapper {
			position: relative;
		}

		&--backText {
			position: absolute;
			top: -20px;
			width: 100%;
			left: 0;
			font-weight: 700;

			-webkit-text-fill-color: transparent;
			-webkit-text-stroke: 1px white;
			opacity: 0.1;
			font-size: 200px;

			@media(max-width:1480px) {
				font-size: 150px;
			}

			@media(max-width:1115px) {
				font-size: 120px;
				top: -30px;
			}

			@media(max-width:992px) {
				font-size: 80px;
				top: -20px;
			}

			@media(max-width:768px) {
				font-size: 60px;
			}

			@media(max-width:576px) {
				font-size: 55px;
				top: -10px;
			}

			@media(max-width:480px) {
				font-size: 40px;
			}
		}

		&--subtitle {
			font-family: var.$font-family;
			font-size: 40px;
			margin-bottom: 30px;
			font-weight: 300;

			@media(max-width:992px) {
				font-size: 30px;
			}

			@media(max-width:768px) {
				font-size: 24px;
			}
		}

		&--title {
			font-size: 96px;
			font-weight: 700;
			text-transform: uppercase;

			@media(max-width:992px) {
				font-size: 70px;
			}

			@media(max-width:768px) {
				font-size: 55px;
			}

			@media(max-width:768px) {
				font-size: 55px;
			}

			@media(max-width:576px) {
				font-size: 45px;
			}

			@media(max-width:480px) {
				font-size: 35px;
			}
		}

		ul {
			display: flex;
			align-items: center;
			justify-content: center;
			gap: 20px;
			margin-bottom: 30px;

			@media(max-width:992px) {
				display: none;
			}

			li {
				position: relative;
				font-size: 14px;
				font-weight: 700;

				&::before {
					width: 3px;
					height: 3px;
					position: absolute;
					content: "";
					left: -8px;
					background-color: white;
					top: 50%;
					transform: translateY(-50%);
				}
			}
		}
	}

	&--col &__content {
		text-align: left;

		@media(max-width: 578px) {
			text-align: center;
		}
	}

	&__colors {
		margin-top: 49px;
		display: flex;
		gap: 15px;

		@media(max-width: 578px) {
			justify-content: center;
		}
	}

	&__color {
		position: relative;
		width: 55px;
		height: 41px;
		border-radius: 29px;
		border: none;
		transition: 0.3s linear;
		padding: 0;
		box-sizing: border-box;
		cursor: pointer;

		@media(max-width:992px) {
			width: 50px;
			height: 36px;
		}

		&::before {
			content: '';
			position: absolute;
			top: -3px;
			left: -4px;
			right: -4px;
			bottom: -3px;
			background: rgba(255, 255, 255, 0.16);
			transition: 0.3s linear;
			z-index: -1;
			border-radius: 32px;
		}

		&--active {
			width: 131px;
			padding: 0;
			outline: none;
			display: block;

			@media(max-width:992px) {
				width: 81px;
			}

			&::before {
				background: rgba(255, 255, 255, 1);
			}
		}

		&:nth-child(1) {
			background: radial-gradient(#686F7E, #444C5F);
		}

		&:nth-child(2) {
			background: radial-gradient(#1A1A1A, #000000);
		}

		&:nth-child(3) {
			background: radial-gradient(#FFFFFF, #FDFDFD);
		}

		&:nth-child(4) {
			background: radial-gradient(#BE2C2C, #961313);
		}
	}

	&__texts {
		transition: 0.3s linear;
		opacity: 0;
		transform: translateY(40px);

		&--active {
			opacity: 1;
			transform: translateY(0);
		}
	}
}